.fugitive__show {
  width: 60%;
  margin: 2rem auto;
  line-height: 1.4;
  .fugitive__name {
    font-weight: 800;
    font-size: 40px;
    margin-bottom: 0.5rem;
  }
  .fugitive__description {
    font-size: 14px;
    margin-bottom: 1.5rem;
  }
  .fugitive__top-field {
    display: flex;
    justify-content: space-between;
    .fugitive__top-person {
      display: inline-block;
      margin: center;
      width: 33%;
      img {
        width: 100%;
        border-radius: 0.5rem;
        margin: center;
      }
    }
    .fugitive__top-poster {
      display: flex;
      flex-direction: column;
      text-align: center;
      a {
        margin-top: 1rem;
        color: black;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
    .fugitive__poster-download {
      h3 {
        font-size: 2rem;
        font-weight: 800;
      }
      ul {
        list-style: square;
        padding-left: 40px;
        font-size: 18px;
        a {
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
  .fugitive__photo-gallery {
    display: block;
    margin-top: 1rem;

    ul {
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 40px;
      li {
        display: inline-block;
        padding: 15px;
        height: auto;
        list-style: none;
        img {
          height: 125px;
        }
      }
    }
  }
  .straight__line {
    width: 100%;
    height: 1px;
    display: block;
    margin: 1.5rem 0;
    background-color: black;
  }
  .fugitive__aliases {
    font-weight: bold;
    margin-top: 0;
    font-size: 2rem;
  }
  .fugitive__aliases-names {
    margin-bottom: 1.5rem;
  }
  table {
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 1.5rem;
    tr {
      border-bottom: 1px solid black;
      &:nth-child(odd) {
        background-color: bisque;
      }

      td {
        display: table-cell;
        padding: 0.5rem;
      }
    }
  }
  .fugitive__reward,
  .fugitive__remarks,
  .fugitive__caution,
  .fugitive__tips {
    margin-bottom: 1.5rem;
    p {
      padding-left: 0.5rem;
    }
  }
  .fugitive__warning-message {
    text-align: center;
    padding: 1.5rem;
    margin: 1.5rem 0;
    background-color: #999;
    p {
      font-weight: 800;
      font-size: 1.6rem;
      color: rgb(168, 30, 5);
    }
  }
  .fugitive__tips {
    .fugitive__field-office {
      display: flex;
      padding-left: 0rem;
      p {
        font-weight: bold;
      }
      span {
        padding-left: 1rem;
      }
    }
    .fugitive__tips-link {
      text-decoration: none;
      color: orangered;
      padding-left: 0.5rem;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
