header {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/earth-banner-background.png);
    background-position: right top;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
}
.brand {
  width: 80vw;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  color: white;
  .brand__field {
    display: flex;
    align-items: center;
    img {
      width: 100px;
      margin: 0 1rem;
    }
    .brand__name {
      display: flex;
      align-items: center;
      margin-left: 1rem;
      .initials {
        font-size: 5rem;
        line-height: 1.6;
        display: inline-block;
        font-weight: 600;
      }
      .fullname {
        text-transform: uppercase;
        display: inline-block;
        font-size: 2rem;
        overflow: hidden;
        line-height: 1em;
        margin-left: 1rem;
      }
    }
  }
  .brand__socials {
    font-size: 1.5rem;
    justify-content: left;
    cursor: pointer;
    padding-right: 1rem;
    .brand__link {
      display: inline;
      width: 30px;
      color: white;
    }
    i {
      width: 50px;
    }
  }
}
.most__wanted {
  display: flex;
  justify-content: center;
}
