.search__results {
  width: 80%;
  margin: 5rem auto auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  display: none; // display
}
.pagination {
  display: flex;
  justify-content: center;
  margin: 2rem auto 1rem;
  align-items: center;
  button {
    padding: 8px 20px;
    border-radius: 8px;
    border: 0;
    cursor: pointer;
    &:active {
      transform: scale(0.98);
    }
  }
  .next {
    margin-left: 2rem;
  }
}
.preview {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  li {
    display: inline-block;
  }
  .preview__link {
    width: 300px;
    max-width: 350px;
    margin: auto;
    padding: 0.5rem;
    display: inline-flex;
    flex-direction: column;
    text-decoration: none;
    overflow: hidden;
  }
  img {
    display: block;
    margin: auto;
    border-radius: 5px;
    height: 300px;
  }
  .preview__data {
    text-align: center;
    color: black;
    display: flex;
    flex-direction: column;

    .preview__title {
      font-size: 18px;
      &:hover {
        text-decoration: underline;
      }
    }
    .preview__crime {
      font-size: 16px;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
