.crime__nav {
  z-index: 5;
  font-weight: 500;

  .crime__list {
    display: flex;
    justify-content: center;
    list-style: none;
    .crime {
      display: inline-block;
      margin: 0;
      font-size: 18px;
      padding: 0.45rem;
      position: relative;
      &:hover {
        background-color: #999;
        transition: background-color 0.5s;
        .subcrime__list {
          animation: slidedown 0.4s;
          display: inline-block;
          .subcrime {
            &:hover {
              background-color: #999;
            }
          }
        }

        svg {
          transform: rotate(-180deg);
          transition: transform 0.5s;
        }
      }
      .subcrime__list {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        margin-top: 2.6rem;
        background-color: rgba(235, 235, 235, 0.904);
        border-left: 5px solid red;

        .subcrime {
          list-style: none;
          display: inline-block;
          padding: 0.3rem 2rem 0.3rem 1rem;
          a {
            display: inline-block;
            color: black;
            width: 200px;
            max-width: 250px;

            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      a {
        text-decoration: none;
        color: black;
      }
      svg {
        color: red;
        cursor: pointer;
      }
    }
  }
}

@keyframes slidedown {
  0% {
    opacity: 0;
    transform: translateY(-18px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
